.count{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 30vw;
    height: 100px;
    border-radius: 6px;
    padding:5px 0 10px 0;
    margin-left: 9px;
    margin-top: 9px;
}
.count_title1{
    padding-top: 10px;
    font-size: 16px;
    font-weight: bolder;
    color: rgb(61, 61, 61);
}
.count_title2{
    font-size: small;
    padding-top: 10px;
    font-weight: bold;
    color: rgb(126, 126, 126);
}
.count_title2>span:last-child{
    padding-left: 5px;
}
.count_title3{
    font-size: small;
    padding-top: 10px;
}
.count_title3>span:last-child{
    font-weight: bolder;
    font-size: 14px;
    padding-left: 5px;
}